.cm-accordion .accordion-header .accordion-button {
  color: #363636;
  font-size: 16px;
  padding-right: 24px;
  text-decoration: none;
  background: #fff;
}
.cm-accordion .accordion-header .accordion-button:after {
  content: "\f068";
  font-family: "fa-solid-900";
  font-size: 14px;
  background-image: none;
  transform: none;
}
.cm-accordion .accordion-header .accordion-button.collapsed:after {
  content: "\f067";
  font-family: "fa-solid-900";
  font-size: 14px;
  background-image: none;
  transform: none;
}

.cm-accordion .accordion-body .field > *:first-child {
  margin-top: 0;
}

.cm-accordion .accordion-body .field > *:last-child {
  margin-bottom: 0;
}